.planets-container {
    margin-left: 1vw;
    margin-top: 2vw;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: repeat(3, .2fr);
    align-items: start;

}

@media screen and (max-width: 1024px) {
.planets-container {
display: grid;
grid-template-columns: repeat(2, .2fr)

}
}

@media screen and (max-width: 425px) {
    .planets-container {
        display: grid;
        grid-template-columns: repeat(1, .2fr)
    }
}